import {showModal, closeModal} from './modal'; 
import {postData} from '../services/services';

function forms(formSelector, setTimeOutModal) {
    //----------------------------------------
    //  
    //----------------------------------------

    const forms = document.querySelectorAll(formSelector); //   

    forms.forEach(item => { //       - postData
        bindPostData(item);
    });

    const message = { //       
        loaded: 'img/054 spinner.svg',
        success: '  !        ',
        error: ' ! -   '
    }

    function bindPostData(form) { //   
        form.addEventListener('submit', (e) => { //     
            e.preventDefault();  //     

            const statusMessage = document.createElement('img'); //       

            statusMessage.src = message.loaded;
            statusMessage.style.cssText = `
                display: block;
                margin: 10px auto;
            `; //      

            form.insertAdjacentElement('afterend', statusMessage); //      

            const formData = new FormData(form); //  FormData
  
            const json = JSON.stringify(Object.fromEntries(formData.entries())); //   FormData  json

            postData('http://localhost:3000/requests', json)//    
            .then(date => {
                console.log(date);
                showThanksModal(message.success);
                statusMessage.remove();
            })
            .catch(() => {
                showThanksModal(message.error);
            })
            .finally(() => {
                form.reset();
            });
        });
    };

    function showThanksModal(message) {
        const prevModalDialog = document.querySelector('.modal__dialog');

        prevModalDialog.classList.add('hide');    
        showModal('[data-modalform]', setTimeOutModal);

        const thanksModal = document.createElement('div');
        thanksModal.classList.add('modal__dialog');
        thanksModal.innerHTML = `
            <div class="modal__content">
                <div class="modal__close" data-closemodal></div>
                <div class="modal__title">${message}</div>
            </div>
        `;

        document.querySelector('.modal').append(thanksModal);
        setTimeout(() => {
            thanksModal.remove();
            prevModalDialog.classList.add('show');
            prevModalDialog.classList.remove('hide');
            closeModal('[data-modalform]');
        }, 4000);
    }
}

export default forms;